@use './variables/font' as font;
@use './variables/color' as color;
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    -webkit-tap-highlight-color: transparent;
    user-select: none;
}

html,
body {
    font-size: 100%;
    position: relative;
    height: 100%;
}

body {
    font-family: font.$font-family;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    background-color: color.$black;
    color: color.$white;
    overflow: hidden;
}

ul,
li {
    list-style-type: none;
}

a {
    color: unset;
    text-decoration: none;
}

iframe {
    border: 0;
}

#root,
.App {
    height: 100%;
}

.bg-image {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.overlay {
    position: relative;
    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba($color: #000000, $alpha: 0.8);
    }
}

.main-color {
    color: color.$main-color;
}

.second-color {
    color: color.$second-color;
}

.title {
    font-weight: 700;
    span {
        font-size: 3rem;
    }
    h2 {
        font-size: 5rem;
        line-height: 5rem;
    }
}

.container {
    max-width: 1670px;
    margin: auto;
}

.m-t-4 {
    margin-top: 4rem;
}

.relative {
    position: relative;
}